.yg-button {
  width: 100px;
  height: 40px;
  background: #000;
}

.yg-pagination {
  display: inline-block;
  vertical-align: middle;
  height: 32px;
}

.yg-pager {
  float: left;
}

.yg-pager__item,
.yg-pager__item--active,
.yg-pager__item--disable {
  float: left;
  width: 32px;
  height: 32px;
  cursor: pointer;
  background: #f5f5f5;
  text-align: center;
  line-height: 32px;
  color: #666;
  font-size: 14px;
  font-family: Arial, sans-serif;
  border-radius: 4px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  margin-right: 8px;
}

.yg-pager__item:hover {
  background: #14c75e;
  color: #fff;
}

.yg-pager__item--active {
  background: #14c75e;
  color: #fff;
}

.yg-pager__item--disable {
  cursor: no-drop;
}

.yg-pager__jump {
  float: left;
  width: 104px;
  height: 32px;
}

.yg-pager__jump__btn {
  float: right;
  width: 56px;
  height: 24px;
  text-align: center;
  line-height: 24px;
  cursor: pointer;
  font-size: 14px;
  color: #666;
  margin-top: 4px;
  background: #f5f5f5;
  border-radius: 4px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.yg-pager__jump__btn:hover {
  background: #14c75e;
  color: #fff;
}

.yg-pager__input {
  float: left;
  margin-right: 5px;
  width: 40px;
  height: 32px;
  border: 1px solid #d9d9d9;
  padding: 0;
  border-radius: 4px;
  text-align: center;
  outline: none;
  font-size: 14px;
  color: #666;
}

.yg-pager__input:focus {
  border-color: #14c75e;
}

.yg-radio {
  display: inline-block;
  color: #656565;
  font-weight: 500;
  line-height: 1;
  cursor: pointer;
  white-space: nowrap;
}

.yg-radio.is-checked {
  color: #14c75e;
}

.yg-radio.is-disabled {
  color: #cbcbcb;
  cursor: no-drop;
}

.yg-radio__input {
  display: inline-block;
  position: relative;
  border-radius: 100%;
  border: 1px solid #d9d9d9;
  width: 16px;
  height: 16px;
  background-color: #fff;
  vertical-align: middle;
}

.yg-radio.is-checked .yg-radio__input {
  border-color: #14c75e;
  background: #14c75e;
}

.yg-radio__input::after {
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: #fff;
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
          transform: translate(-50%, -50%) scale(0);
  -webkit-transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6), -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
}

.yg-radio.is-checked .yg-radio__input::after {
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
}

.yg-radio.is-disabled .yg-radio__input {
  background-color: #f5f5f5;
  border: 1px solid #d9d9d9;
}

.yg-radio.is-disabled .yg-radio__input::after {
  background-color: #c0c4cc;
}

.yg-radio:not(.is-disabled) .yg-radio__input:hover {
  border-color: #14c75e;
}

.yg-radio__label {
  font-size: 12px;
  padding-left: 4px;
  line-height: 16px;
  vertical-align: middle;
}

.yg-checkbox {
  display: inline-block;
  color: #656565;
  font-weight: 500;
  line-height: 1;
  cursor: pointer;
  white-space: nowrap;
}

.yg-checkbox.is-checked {
  color: #14c75e;
}

.yg-checkbox.is-disabled {
  color: #cbcbcb;
  cursor: no-drop;
}

.yg-checkbox__input {
  display: inline-block;
  position: relative;
  border: 1px solid #d9d9d9;
  width: 14px;
  height: 14px;
  background-color: #fff;
  vertical-align: middle;
}

.yg-checkbox.is-checked .yg-checkbox__input {
  border-color: #14c75e;
  background: #14c75e;
}

.yg-checkbox__input::after {
  content: "";
  border: 1px solid #fff;
  border-left: 0;
  border-top: 0;
  height: 7px;
  left: 4px;
  position: absolute;
  top: 1px;
  -webkit-transform: rotate(45deg) scaleY(0);
          transform: rotate(45deg) scaleY(0);
  width: 3px;
  -webkit-transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6) 50ms;
  transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6) 50ms;
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6) 50ms;
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6) 50ms, -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6) 50ms;
}

.yg-checkbox.is-checked .yg-checkbox__input::after {
  -webkit-transform: rotate(45deg) scaleY(1);
          transform: rotate(45deg) scaleY(1);
}

.yg-checkbox.is-disabled .yg-checkbox__input {
  border-color: #e5e5e5;
  background: #f4f4f4;
}

.yg-checkbox.is-disabled.is-checked .yg-checkbox__input::after {
  border-color: #dadada;
  -webkit-transform: rotate(45deg) scaleY(1);
          transform: rotate(45deg) scaleY(1);
}

.yg-checkbox:not(.is-disable) .yg-checkbox__input:hover {
  border-color: #14c75e;
}

.yg-checkbox__label {
  font-size: 12px;
  padding-left: 4px;
  line-height: 16px;
  vertical-align: middle;
}

.page {
  width: 100%;
}
.home-content {
  padding: 40px 40px 40px 240px;
  width: 100%;
}

.menu {
  position: fixed;
  left: 0;
  top: 0;
  width: 240px;
  height: 100%;
  padding-left: 30px;
  padding-top: 50px;
}
.nav-item a {
  display: block;
  height: 40px;
  color: #444;
  line-height: 40px;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 400;
}
